<template lang="pug">
    v-toolbar(dark app fixed clipped-left)
        v-btn(icon @click.stop="$emit('toggleDrawer')")
            v-icon mdi-menu
        v-toolbar-title {{ title }}
        v-spacer
        v-btn(icon to="/")
            v-icon mdi-home
        v-btn(icon @click.stop="logoutDialog = true")
            v-icon mdi-logout
            v-dialog(v-model="logoutDialog" width="300px")
                v-card
                    v-card-title.subheading {{ logoutTitle }}
                    v-card-text {{ logoutText }}
                    v-card-actions
                        v-spacer
                        v-btn(flat color="red darken-1" @click="logoutDialog = false") {{ logoutCancel }}
                        v-btn(flat color="green darken-1" @click="logoutDialog = false; logout()") {{ logoutConfirm }}
</template>

<script>
    export default {
        name: 'UserToolbar',
        props: {
            title: {
                type: String,
                required: true
            }
        },
        data () {
            return {
                logoutDialog: false,
                logoutTitle: '?',
                logoutText: '',
                logoutCancel: '',
                logoutConfirm: '',
            }
        },
        methods: {
            async logout () {
                let self = this;
                this.$store.dispatch('logout')
                    .then(() => self.$router.push('/'))
                    .catch(() => {});
            }
        }
    }
</script>

<style scoped>

</style>
