<template lang="pug">
    v-navigation-drawer(
        app
        clipped
        fixed
        v-model="drawer"
        :mini-variant.sync="drawerMini"
        width="250"
    )
        v-list(two-line)
            v-list-tile(avatar)
                v-list-tile-avatar(tile)
                    UserAvatar
                v-list-tile-content
                    v-list-tile-title {{ user.nickname || 'Guest' }}
                    v-list-tile-sub-title(:class="getUserPermissionClass")
                        |
                        v-icon(:class="getUserPermissionClass" style="font-size: 1.14rem;") mdi-brightness-1
                        | {{ getUserPermissionText }}
                v-list-tile-action.hidden-xs-only
                    v-btn(icon @click.stop="drawerMini = !drawerMini")
                        v-icon mdi-chevron-left
        v-divider
        v-list(v-if="links")
            v-list-tile(v-for="link in links" :key="link.title" :to="link.to" exact)
                v-list-tile-action
                    v-icon {{ link.icon }}
                v-list-tile-content
                    v-list-tile-title {{ link.title }}
</template>

<script>
    import UserAvatar from '@/components/user/UserAvatar.vue'
    import { mapGetters } from 'vuex'
    export default {
        name: 'UserDrawer',
        components: {
            UserAvatar
        },
        props: {
            links: {
                type: Array,
                default: null
            }
        },
        data () {
            return {
                drawer: null,
                drawerMini: false
            }
        },
        computed: {
            ...mapGetters([
                'getUserPermissionClass',
                'getUserPermissionText'
            ]),
            user () {
                return this.$store.state.user;
            }
        },
        methods: {
            toggle () {
                this.drawer = !this.drawer;
            },
        }
    }
</script>

